import React, { Component } from 'react'
import p_search from '../../image/search.png'
import './Header.css'
import { NavLink} from 'react-router-dom'
import { scroller } from 'react-scroll';

export default class Header extends Component {
  scrollToElement = (id) => {
    scroller.scrollTo(id, {
      duration: 800,
      delay: 0,
      smooth: 'easeInOutQuart',
    });
  };

  render() {

    return (
      <div>
        <header>
          <nav id="headerNav">
            <ul className='headerUl'>
              <NavLink className='headerList' to="/index">Home</NavLink>

              <div className='headerDiv'>
                <p>Team members:</p>
                <ul>
                  <li>name: Rui Fang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012748</li>
                  <li>name: Peidong Miao &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012659</li>
                  <li>name: Xuefeng Huo &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012752</li>
                  <li>name: Zhaohui Liang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012755</li>
                  <li>name: Baichuan Zhang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012651</li>
                </ul>
              </div>

              <a className='headerList' href='http://wjbaby.housescheck.com/' alt="" > Map </a>

              <div className='headerDiv' href="#">
                <p>On the map page, you can use the following functions:</p>
                <ul>
                  <li>Generate a data chart based on XGBoost algorithm when a location is clicked.</li>
                  <li>Generate a data chart based on GradientBoostingRegression algorithm when a location is clicked.</li>
                  <li>Generate a data chart based on RandomForest algorithm when a location is clicked.</li>
                  <li>Toggle visibility of location markers on the map.</li>
                  <li>Toggle visibility of the heatmap overlay.</li>
                  <li>Toggle visibility of heatmap based on area and time overlay.</li>
                </ul>
              </div>

              <NavLink className='headerList' to="/index" onClick={() => this.scrollToElement('section1')}>Display</NavLink>

              <div className='headerDiv'>
                <p>There will be a display of the houses<br /> we recommend on the home page:</p>
                <ul>
                  <li>You can see beautiful pictures and simple introduction</li>
                  <li>Click the icon below to enjoy the silky smooth picture switching effect</li>
                  <li>Click "learn more" for more information</li>
                  <li>Every once in a while we will recommend different room rates</li>
                  <li>If you haven’t set a goal, you might as well come here and have a look, you might be surprised.</li>
                </ul>
              </div>

              <NavLink className='headerList' to="/search">Search</NavLink>

              <div className='headerDiv'>
                <p>Search for the house information <br />you may need through the search box on the right:</p>
                <ul>
                  <li>Enter keywords and click the icon to search</li>
                  <li>Then the page will pop up all the house data that can be retrieved</li>
                  <li>If not found, you will also be notified</li>
                  <li>You can try different keywords to search for houses</li>
                </ul>
              </div>

              <div id='divLine'></div>
            </ul>
          </nav>

          <div className="search_box">
            <NavLink id='search' to="/search">
              <img src={p_search} alt=""/>
            </NavLink>
            
          </div>

        </header>

        
      </div>


    )
  }
}
