import React, { Component } from 'react'
import './Footer.css'

export default class Footer extends Component {
  render() {
      return (
          <div>
              <div className="header">
                  <div className="inner-header flex">
                    <p id='useP'>Get started with House Price Prediction now</p>
                    <p id='small'>Complete data&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; | &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;rich functions&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;| &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;convenient use</p>
                  </div>
                  <div>
                      <svg className="waves" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink"
                          viewBox="0 24 150 28" preserveAspectRatio="none" shapeRendering="auto">
                          <defs>
                              <path id="gentle-wave"
                                  d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
                          </defs>
                          <g className="parallax">
                              <use xlinkHref="#gentle-wave" x="48" y="0" fill="rgba(58, 74, 99,0.7)" />
                              <use xlinkHref="#gentle-wave" x="48" y="3" fill="rgba(58, 74, 99,0.5)" />
                              <use xlinkHref="#gentle-wave" x="48" y="5" fill="rgba(58, 74, 99,0.3)" />
                              <use xlinkHref="#gentle-wave" x="48" y="7" fill="rgb(58, 74, 99)" />
                          </g>
                      </svg>
                  </div>
              </div>
          </div>
    )
  }
}
 