import React, { Component } from 'react'
import './Display.css'
import p1 from '../../image/1.jpg'
import p2 from '../../image/2.jpg'
import p3 from '../../image/3.jpg'
import p4 from '../../image/4.jpg'
import p5 from '../../image/5.jpg'
import p6 from '../../image/6.jpg'

import { withRouter } from 'react-router-dom';

class Display extends Component {

    left = () => {
        let lists = document.querySelectorAll('.item');
        document.querySelector('#slide').appendChild(lists[0]);
    }

    right = () => {
        let lists = document.querySelectorAll('.item');
        document.querySelector('#slide').prepend(lists[lists.length - 1]);
    }

    render() {
        return (
            <div id='container'>
                <div id='slide' >
                    <div className='item' style={{ backgroundImage: `url(${p1}` }}>
                        <div className='content'>
                            <p className='name'>
                                Elegantly Furnished Home
                            </p>
                            <p className='description'>
                                Despite its modest price, this home boasts a beautifully finished interior.</p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>

                    <div className='item' style={{ backgroundImage: `url(${p2}` }}>
                        <div className='content'>
                            <p className='name'>
                                Downtown Penthouse
                            </p>
                            <p className='description'>
                                Prices have been steadily increasing in recent years, promising substantial investment potential.
                            </p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>

                    <div className='item' style={{ backgroundImage: `url(${p3}` }}>
                        <div className='content'>
                            <p className='name'>
                                Modern Urban Apartment
                            </p>
                            <p className='description'>
                                The apartment features a contemporary design, equipped with advanced smart home systems, representing a stylish urban lifestyle.</p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>

                    <div className='item' style={{ backgroundImage: `url(${p4}` }}>
                        <div className='content'>
                            <p className='name'>
                                Lakeside Holiday Home
                            </p>
                            <p className='description'>
                                Prices remain relatively stable, but it's a popular holiday destination, loved by vacation and leisure enthusiasts.</p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>

                    <div className='item' style={{ backgroundImage: `url(${p5}` }}>
                        <div className='content'>
                            <p className='name'>
                                Rural Countryside Retreat
                            </p>
                            <p className='description'>
                                The villa is surrounded by vast greenery and lakes, an excellent spot for a healthy lifestyle and family leisure.</p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>

                    <div className='item' style={{ backgroundImage: `url(${p6}` }}>
                        <div className='content'>
                            <p className='name'>
                                High-Rise View Apartment
                            </p>
                            <p className='description'>
                                This apartment occupies one of the highest floors, providing unobstructed views of the cityscape, making it a prestigious choice for urban dwellers who appreciate stunning vistas.</p>
                            <button>
                                See More
                            </button>
                        </div>
                    </div>
                </div>

                <div className='buttons'>
                    <button className='but' onClick={this.left}>&lt;</button>
                    <button className='but' onClick={this.right}>&gt;</button>
                </div>

            </div>
        )
    }
}

export default withRouter(Display);
