import React, { Component } from 'react'
import './NewSearch.css'
import { NavLink } from 'react-router-dom'
import { scroller } from 'react-scroll';
import Papa from 'papaparse';

import p_search from '../../image/search.png'
import p1 from '../../image/house6.jpg'
import p2 from '../../image/house2.jpg'
import p3 from '../../image/house3.jpg'
import p4 from '../../image/house4.jpg'
import p5 from '../../image/house5.jpg'
import p6 from '../../image/house7.jpg'
import p7 from '../../image/house8.jpg'
import p8 from '../../image/house9.jpg'
import p9 from '../../image/house0.jpg'
import p0 from '../../image/house1.jpg'

export default class NewSearch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inputValue: '',
            searchResults: [],
            judge: false,
            none: false
        };
    }

    scrollToElement = (id) => {
        scroller.scrollTo(id, {
            duration: 800,
            delay: 0,
            smooth: 'easeInOutQuart',
        });
    };

    handleInputChange = (e) => {
        this.setState({ inputValue: e.target.value });
    };

    handleSearch = () => {
        this.setState({ judge: true });
        Papa.parse('./clean_data.csv', {
            download: true,
            complete: (result) => {
                // 使用filter方法筛选出包含用户输入内容的行
                const filteredRows = result.data.filter(row =>
                    row.some(cell => cell.toLowerCase().includes(this.state.inputValue.toLowerCase()))
                );

                // 将匹配的行按照单元格数据拆分并存储为数组
                const formattedResults = filteredRows.map(row => {
                    // 对于每一行，将包含输入内容的单元格拆分成数组元素
                    const formattedRow = row.map(cell => {
                        return cell.split(','); // 假设CSV文件中单元格数据以逗号分隔
                    });
                    return formattedRow;
                });

                // 将拆分后的数据存储在组件的searchResults状态中
                this.setState({ searchResults: formattedResults });
                if (formattedResults.length == 0) {
                    this.setState({ none: true });
                    var element = document.getElementById("no");
                    element.style.display = "block";
                }
                if (formattedResults.length > 0) {
                    this.setState({ none: false });
                    var element = document.getElementById("no");
                    element.style.display = "none";
                }
            },
        });

    };
    render() {
        const images = [p0, p1, p2, p3, p4, p5, p6, p7, p8, p9];
        const numberOfImages = 10;
        return (
            <div>
                <header>
                    <nav id="headerNav">
                        <ul className='headerUl'>
                            <NavLink className='headerList' to="/index">Home</NavLink>

                            <div className='headerDiv'>
                                <p>Team members:</p>
                                <ul>
                                    <li>name: Rui Fang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012748</li>
                                    <li>name: Peidong Miao &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012659</li>
                                    <li>name: Xuefeng Huo &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012752</li>
                                    <li>name: Zhaohui Liang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012755</li>
                                    <li>name: Baichuan Zhang &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;student id: 21012651</li>
                                </ul>
                            </div>

                            <a className='headerList' href='http://wjbaby.housescheck.com/' alt="" > Map </a>

                            <div className='headerDiv' href="#">
                                <p>On the map page, you can use the following functions:</p>
                                <ul>
                                    <li>Generate a data chart based on XGBoost algorithm when a location is clicked.</li>
                                    <li>Generate a data chart based on GradientBoostingRegression algorithm when a location is clicked.</li>
                                    <li>Generate a data chart based on RandomForest algorithm when a location is clicked.</li>
                                    <li>Toggle visibility of location markers on the map.</li>
                                    <li>Toggle visibility of the heatmap overlay.</li>
                                    <li>Toggle visibility of heatmap based on area and time overlay.</li>
                                </ul>
                            </div>

                            <NavLink className='headerList' to="/index" onClick={() => this.scrollToElement('section1')}>Display</NavLink>

                            <div className='headerDiv'>
                                <p>There will be a display of the houses<br /> we recommend on the home page:</p>
                                <ul>
                                    <li>You can see beautiful pictures and simple introduction</li>
                                    <li>Click the icon below to enjoy the silky smooth picture switching effect</li>
                                    <li>Click "learn more" for more information</li>
                                    <li>Every once in a while we will recommend different room rates</li>
                                    <li>If you haven’t set a goal, you might as well come here and have a look, you might be surprised.</li>
                                </ul>
                            </div>

                            <NavLink className='headerList' to="/search">Search</NavLink>

                            <div className='headerDiv'>
                                <p>Search for the house information <br />you may need through the search box on the right:</p>
                                <ul>
                                    <li>Enter keywords and click the icon to search</li>
                                    <li>Then the page will pop up all the house data that can be retrieved</li>
                                    <li>If not found, you will also be notified</li>
                                    <li>You can try different keywords to search for houses</li>
                                </ul>
                            </div>

                            <div id='divLine'></div>
                        </ul>
                    </nav>

                    <div className="search_box">
                        <input
                            type="text"
                            className="search_txt"
                            value={this.state.inputValue}
                            onChange={this.handleInputChange}
                            placeholder='Search by location' />

                        <div id='search' >
                            <img src={p_search} alt="" onClick={this.handleSearch} />
                        </div>

                    </div>

                </header>

                {this.state.judge ? <p className='judge'>The following are the search results:</p> : <p className='judge'>Please enter the keyword you want to search in the input box in the upper right corner</p>}
                <div id='no'>
                    Sorry, there are no search results. <br/>
                    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Please try other keywords.
                </div>

                <div className="house-container">
                    {this.state.searchResults.slice(1).map((row, rowIndex) => (
                        <div key={rowIndex} className="house">
                            <div className="house-content">
                                <h1 className="house-name">{rowIndex + 1}. {row[13]}</h1>
                                <div id='box'>
                                    <img
                                        className="house-image"
                                        src={images[rowIndex % numberOfImages]}
                                        alt=""
                                    />
                                    <div className="house-info">
                                        <div className="house-description">1. Price(1000$):{row[0]}</div>
                                        <div className="house-description">2. Bedromms:{row[1]}</div>
                                        <div className="house-description">3. Bathrooms:{row[2]}</div>
                                        <div className="house-description">4. Living area(square meters):{row[3]}</div>
                                        <div className="house-description">5. Land area(square meters):{row[4]}</div>
                                        <div className="house-description">6. Construction time(years):{row[11]}</div>
                                        <div className="house-description">7. Last renovation time(years):{row[12]}</div>
                                        <div className="house-description">8. living_ratio(Living/Land):{row[17]}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

            </div>
        )
    }
}
